import React, { useState } from "react";
import { Col, Row, Button } from 'react-bootstrap';
import { useParams, useNavigate } from 'react-router-dom';
import Accordion from 'react-bootstrap/Accordion';
import { useDispatch } from "react-redux";

import data_all from "../data_all";
import { addItem } from "../store.js";
import '../scss/detail.scss'
import Product from "../components/Product";

import { Swiper, SwiperSlide } from 'swiper/react';
import { Scrollbar } from 'swiper/modules';
import { Virtual } from 'swiper/modules';
import 'swiper/css';
import 'swiper/css/scrollbar';


const Detail = (props) => {
  const { tamburins, setTamburins } = props;
  const { id } = useParams();
  console.log(tamburins, id)
  const selproduct = tamburins.find(x => x.id == id)
  const relproduct = tamburins.filter(x => x.id !== id && x.category === selproduct.category)
  console.log(relproduct)

  const origin = data_all;
  const filterItem = (category) => {
    const copy = [...origin].filter(v => v.category == category);
    setTamburins(copy);
  }

  const navigate = useNavigate();
  const dispatch = useDispatch();


  const slides = Array.from({ length: 1000 }).map(
    (el, index) => `Slide ${index + 1}`
  );

  const moveCart = () => {
    dispatch(addItem({
      id: selproduct.id, imgUrl: selproduct.imgUrl, item: selproduct.title, content: selproduct.content
      , price: selproduct.price, amount: 1
    }));
    navigate('/cart');
  }


  return (
    <>
      <div className='detailWrap'>
        <Row className="detail_inner">
          <Col md={6} className='product'>
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl} width="100%" />
            <img src={process.env.PUBLIC_URL + selproduct.imgUrl_sub} width="100%" />
          </Col>

          <Col md={6} className='buy'>
            <div className="info">
              <div className="product_detail">
                <h4 className="title">{selproduct.title}</h4>
                <p className="price"> {selproduct.price.toLocaleString()}</p>
                <p className="content">{selproduct.content}</p>
                <p className="type">{selproduct.type}</p>
                <div className="more_product">
                  <Swiper 
                  modules={[Virtual]} 
                  spaceBetween={10} 
                  slidesPerView={5} 
                  virtual
                  breakpoints={{
                    1800: {
                      slidesPerView: 5,
                    },
                    1500: {
                      slidesPerView: 4,
                    },
                    1200: {
                      slidesPerView: 3,
                    },
                    768: {
                      slidesPerView: 3,
                    },
                    500: {
                      slidesPerView: 2,
                    },
                    320: {
                      slidesPerView: 2,
                    },
                  }}
                  >

                    {relproduct.map((rel, i) => (
                      <SwiperSlide key={rel} virtualIndex={i} onClick={() => {navigate('/detail/' + rel.id)}}>
                        {<img src={process.env.PUBLIC_URL + rel.imgUrl} width="100%" onClick={() => { }} />}
                        {rel.title}
                      </SwiperSlide>
                    ))}
                  </Swiper>
                </div>
                <p className="size"></p>
                <p className="gram"><span>{selproduct.size}</span></p>
                <Button variant="dark" onClick={moveCart}> </Button>

                <Accordion>
                  <Accordion.Item eventKey="0">
                    <Accordion.Header> </Accordion.Header>
                    <Accordion.Body className="details">
                      <div className="txtBox">
                        <span></span>
                        <p>    .</p>
                      </div>
                      <div className="txtBox">
                        <span>  </span>
                        <p>1.      . <br />
                          2.        . <br />
                          3.           . <br />
                          *              .</p>
                      </div>
                      <div className="txtBox">
                        <span></span>
                        <p> 24(    12    .)</p>
                      </div>
                      <div className="txtBox">
                        <span></span>
                        <p>()</p>
                      </div>
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item eventKey="1">
                    <Accordion.Header className="delivery">  </Accordion.Header>
                    <Accordion.Body>
                      <p>3      .</p>
                      <p> 1-2   .</p>
                      <p>         .    ,       . <br />
                        CJ(https://www.cjlogistics.com)</p>
                      <p>   ()      .</p>
                      <p>       .   .</p>
                    </Accordion.Body>
                  </Accordion.Item>
                  <Accordion.Item eventKey="2">
                    <Accordion.Header>  </Accordion.Header>
                    <Accordion.Body className="shoppingBag">
                      <img src={process.env.PUBLIC_URL + '/img/shoppingBag.jpg'} width='100%' />
                      <p><b> S .</b></p>
                      <p>          1:1        .</p>
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
              </div>
            </div>
          </Col>
        </Row>

        <div className="recommend">
          <div className='together'>
            <h4>MAYBE YOU LIKE</h4>
            <Swiper
              className="recommend_swiper"
              spaceBetween={'20px'}
              slidesPerView={5}
              scrollbar={{ hide: true, }}
              modules={[Scrollbar]}
              breakpoints={{
                1800: {
                  slidesPerView: 5,
                },
                1500: {
                  slidesPerView: 4,
                },
                1200: {
                  slidesPerView: 3,
                },
                768: {
                  slidesPerView: 2,
                },
                200: {
                  slidesPerView: 1,
                },
              }} >

              {origin.map((tamburin, i) => (
                <SwiperSlide className="recommend_slide" key={tamburins} virtualIndex={i}>
                  <Product tamburins={tamburin} i={i} />
                </SwiperSlide>
              ))}
            </Swiper>
          </div>
        </div>
      </div>
    </>
  )
}

export default Detail;