import React from 'react'
import { Tab, Tabs, Form, InputGroup, Button } from 'react-bootstrap';
import '../scss/login.scss'


function Logincheck(id2, pw2) {
    var reg = /^(?=.*?[A-Z])(?=.*?[a-z])(?=.*?[0-9])(?=.*?[#?!@$%^&*-]).{8,}$/;
    var hangulcheck = /[-|-|-]/;

    if (false === reg.test(pw2)) {
        alert(' 8  , ///   .');
    } else if (/(\w)\1\1\1/.test(pw2)) {
        alert('  4    .');
    } else if (pw2.search(id2) > -1) {
        alert("  .");
    } else if (pw2.search(/\s/) != -1) {
        alert("   .");
    } else if (hangulcheck.test(pw2)) {
        alert("     .");
    } else {
        console.log("");
    }
}

const Login = () => {
    const handleSubmit = (e) => {
        e.preventDefault();
        const pw2 = document.getElementById("pw1").value;
        const id2 = document.getElementById("id1").value;
        Logincheck(id2, pw2);
    };

    return (
        <>
            <div className='logWrap'>
                <Tabs mdefaultActiveKey="profile" id="fill-tab-example" className="mb-3" fill>
                    <Tab eventKey="home" title="">
                        <InputGroup className="mb-3 my-5">
                            <Form.Control
                                aria-label="Default"
                                aria-describedby="inputGroup-sizing-default"
                                placeholder="()"
                                id='id1'
                            />
                        </InputGroup>
                        <InputGroup className="mb-3">
                            <Form.Control
                                aria-label="Default"
                                aria-describedby="inputGroup-sizing-default"
                                placeholder=""
                                id='pw1'
                            />
                        </InputGroup>
                        <Button variant="dark" className='start' onClick={handleSubmit}></Button>
                        <div className='find'><p> </p> <b>|</b> <p> </p> <b>|</b> <p></p></div>
                        <Button variant="dark" className='kakao'><img src={process.env.PUBLIC_URL + '/img/kakao.png'} width='5%' /> </Button><br />
                        <Button variant="dark" className='naver'><img src={process.env.PUBLIC_URL + '/img/naver.png'} width='5%' />  </Button>
                    </Tab>

                    <Tab eventKey="profile" title=" ">
                        <p className='mt-5 number'> </p>
                        <InputGroup className="mb-3">
                            <Form.Control
                                aria-label="Default"
                                aria-describedby="inputGroup-sizing-default"
                                placeholder="'-'  "
                            />
                        </InputGroup>

                        <p className='mt-4 number'> </p>
                        <InputGroup className="mb-5">
                            <Form.Control
                                aria-label="Default"
                                aria-describedby="inputGroup-sizing-default"
                                placeholder="18  "
                            />
                        </InputGroup>
                        <Button variant="dark" className='orderFind'></Button>
                        <Button variant="dark" className='join'></Button>
                    </Tab>
                </Tabs>
            </div>
        </>
    )
}

export default Login